* {
	font-family: Helvetica, Arial, sans-serif;
}

body{
	padding: 60px;
}

input{
	width: 100%;
	color: grey;
	font-size: 1.2rem;
}

.card{
	align-content: center;
	border: 1px solid grey;
	border-radius: 5px;
	margin: 10px;
	overflow: hidden;
}

.card p{
	padding: 5px;
	text-align: center;
}

.card img{
	max-height: 170px;
	min-height: 170px;
}

.card-grid{
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	flex-wrap: wrap;
}